import React, { Fragment} from 'react';
import Links from './links.js';
import ContactMe from './contact.js';
import './social.css';
import {FormattedMessage} from 'react-intl';


export default function Social(){
	const linkList = [
	{
		key: 1,
		icon: "fab fa-facebook-f fa-stack-1x",
		link: "https://www.facebook.com/pereztdaniel"
	},
	{
		key: 2,
		icon: "fab fa-instagram fa-stack-1x",
		link: "https://www.instagram.com/pereztdaniel/"
	},
	{
		key: 3,
		icon: "fab fa-linkedin fa-stack-1x",
		link: "https://www.linkedin.com/in/pereztdaniel"
	},
	{
		key: 4,
		icon: "fab fa-github fa-stack-1x",
		link: "https://github.com/Pereztdaniel"
	}]


	return (
	<>
		<section id="contact">
			<div className="info">
				<h2>
					<FormattedMessage
						id="contact.info"
						defaultMessage="Contact Info"
					/>
				</h2>
				<h3>
					<FormattedMessage
						id="contact.subtitle"
						defaultMessage="Get in Touch With Me"
					/>
				</h3>
				<p>
					<FormattedMessage
						id="contact.text"
						defaultMessage="Any question? use the contact form bellow to send me a message and i will try to get back to you in 24 hours."
					/>
				</p>
				<br/>
				<h3>
					<FormattedMessage
						id="follow"
						defaultMessage="Follow Daniel Pérez on Social Media"
					/>
				</h3>
				<div>
					{linkList.map(({ key, icon, link }) => (
						<Links key={key} icon={icon} link={link} />
					))}
				</div>
			</div>

			<div className="info_form">
				<ContactMe/>
			</div>

		</section>
	</>
);
}