import React, {Fragment} from 'react';
import Repository from './repository';
import './project.css';
import travelBlog from './../../img/travelblog.jpg';
import eLandingPage from './../../img/Frontend-mentor.jpg';
import indexedDB from './../../img/indexeddb.jpg';
import calc from './../../img/calculadora.jpg';
import psicologia from './../../img/psicologia.jpg';
import catalogo from './../../img/catalogo.jpg';

import {FormattedMessage} from 'react-intl';

export default function Project(){

	const repositoryList= [
	{
		key: 1,
		img: psicologia,
		title: 'Psicologa María Mujica',
		link: 'https://psicologia.netlify.app/'
	},
	{
		key: 2,
		img: catalogo,
		title: 'Catalógo Digital',
		link: 'https://catalogodigital.netlify.app/'
	},
	{
		key: 3,
		img: travelBlog,
		title: 'Travel Blog',
		link: 'https://pereztdaniel.github.io/album/'
	},
	{
		key: 4,
		img: eLandingPage,
		title: 'Ecommerce LandingPage',
		link: 'https://github.com/Pereztdaniel/Frontend-mentor-challenge'
	},{
		key: 5,
		img: indexedDB,
		title: 'CRUD with IndexedDB',
		link: 'https://github.com/Pereztdaniel/crud-with-indexeddb'
	},
	{
		key: 6,
		img: calc,
		title: 'Calculator',
		link: 'https://github.com/Pereztdaniel/calculadora'
	}]

	return (
	<>
		<div id="project">
			<h2 className="moveUp">
				<FormattedMessage
						id="title.project" 
						defaultMessage="PROJECTS" 
				/>
			</h2>
			<div className="news-cards moveUp">
				{repositoryList.map(({ img, title, key, link }) => (
					<Repository
						img={img}
						key={key}
						title={title}
						link={link}
					/>
				))}
			</div>
		</div>
	</>
);
}